import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App.vue'
import VueRouter from 'vue-router'
import VueSession from 'vue-session'
import fontawesome from '@fortawesome/fontawesome'
import brands from '@fortawesome/fontawesome-free-brands'
import faSolid from '@fortawesome/fontawesome-free-solid'
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import VueCarousel from 'vue-carousel'

import VueI18n from 'vue-i18n'
// import Cookies from 'js-cookie'
// import elementEnLocale from 'element-ui/lib/local/lang/en' // element-ui lang
// import elementZhLocale from 'element-ui/lib/local/lang/zh-CN'// element-ui lang
import enLocale from './lang/en'
import frLocale from './lang/fr'
import esLocale from './lang/es'

import Home from './views/Home'
import Approach from './views/Approach'
import Development from './views/Development'
import Projects from './views/Projects'
import Pricing from './views/Pricing'
import Career from './views/Career'

var pers = {
  persist: true
}
Vue.use(VueRouter)
Vue.use(ElementUI)
Vue.use(VueSession, pers)
Vue.use(VueCarousel)
Vue.use(VueI18n)
const messages = {
  en: {
    ...enLocale
  },
  fr: {
    ...frLocale
  },
  es: {
    ...esLocale
  }
}
const i18n = new VueI18n({
  locale: 'en',
  messages
})
fontawesome.library.add(brands, faSolid)
Vue.component('font-awesome-icon', FontAwesomeIcon)
require('./assets/global.css')
const router = new VueRouter({
  mode: 'history',
  routes: [
    // { path: '*', redirect: '/', },
    { path:   '/', component: Home },
    { path:   '/approach', component: Approach },
    { path:   '/development', component: Development },
    { path:   '/projects', component: Projects },
    { path:   '/pricing', component: Pricing },
    { path:   '/career', component: Career }
    /* { path: '/about', component: About },
    { path: '/users', component: Users,
      children: [
        { path: ':username', name: 'user', component: User }
      ]
    } */
  ]
})

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  i18n,
  components: { App }
})






