<script>
export default {
    props:['grey','langpass'],
    data () {
        return {
            curlang: '',
            year: ''
        }
    },
    beforeCreate: function () {
        if (this.$session.has('langDef')) {
            this.$router.push(this.$session.get('langDef'))
        }
    },
    created (){
        /* if (this.$session.has('langDef')) {
            this.curlang = this.$session.get('langDef').toString()
        } else { */
        this.curlang = 'en'
        // }
        this.year = new Date().getFullYear()
    },
    watch: {
      langpass: function (val) {
        this.curlang !== val ? this.curlang = val : this.curlang
      }
    },
    methods: {
        handleSetLanguage(lang) {
            if (lang === 'en') {
              this.$router.push('/en')
              this.$message({
                message: 'language switch success',
                type: 'success'
              })
              this.$session.set('langDef','en')
              this.curlang = 'en'
            } else if (lang === 'fr') {
              this.$router.push('/fr')
              this.$message({
                message: 'changement de langue',
                type: 'success'
              })
              this.$session.set('langDef','fr')
              this.curlang = 'fr'
            } else if (lang === 'es') {
              this.$router.push('/es')
              this.$message({
                message: 'Cambio de idioma éxito',
                type: 'success'
              })
              this.$session.set('langDef','es')
              this.curlang = 'es'
            }
            this.$emit('language', this.curlang)
        }
    }
}
</script>
