        <el-form-item label="Email / Phone" prop="contact" label-position="top">
            <el-input v-model="auth.contact" placeholder="Email / International Phone Number"></el-input>
        </el-form-item>
        <el-form-item label="Project Description" prop="description" label-position="top">
            <el-input type="textarea" v-model="auth.description" placeholder="Project description"></el-input>
        </el-form-item>
        <el-form-item style="padding-top:15px;">
            <el-button type="primary" long @click="submitForm('auth')">{{$t('estimation.button')}}</el-button>
        </el-form-item>
      </el-form>

  </el-row>
  <span slot="footer" class="dialog-footer">
  </span>
</el-dialog>

<el-dialog
  :visible.sync="hireDialog"
  center style="z-index: 99999999 !important;" :before-close="handlehr">
  <div slot="title" class="kesa-modal-title">
  <h2>{{$t('hire.title')}}</h2>
    <span>{{$t('hire.info')}}</span>
  </div>
  <el-row>
    
    <el-form ref="hire" :model="auth" :rules="ruleValidate">
          <el-form-item label="Type of project" prop="type" label-position="top">
            <br/>
            <el-select style="width:100%;" v-model="auth.type" placeholder="Select">
    <el-option
      v-for="item in options_sel"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
        </el-form-item>
        <el-form-item label="Fullname" prop="name" label-position="top">
            <el-input v-model="auth.name" placeholder="Fullname"></el-input>
        </el-form-item>
        <el-form-item label="Email / Phone" prop="contact" label-position="top">
            <el-input v-model="auth.contact" placeholder="Email / International Phone Number"></el-input>
        </el-form-item>
        <el-form-item label="Project Description" prop="description" label-position="top">
            <el-input type="textarea" v-model="auth.description" placeholder="Project description"></el-input>
        </el-form-item>
        <el-form-item style="padding-top:15px;">
            <el-button type="primary" long @click="submitForm('hire')">{{$t('hire.button')}}</el-button>
        </el-form-item>
      </el-form>

  </el-row>
  <span slot="footer" class="dialog-footer">
  </span>
</el-dialog>
</div>
</template>
<script>
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)
export default {
    props:['grey','langpass','hr','est'],
    data () {
        return {
            estimateDialog: false,
            hireDialog: false,
            types: '',
            curlang: '',
            hash:'',
            navBtn: false,
            options_sel: [{
          value: 'Web Applications',
          label: 'Web Applications'
        }, {
          value: 'Mobile Applications',
          label: 'Mobile Applications'
        }, {
          value: 'Enterprise Applications',
          label: 'Enterprise Applications'
        }, {
          value: 'Content Writing',
          label: 'Content Writing'
        }, {
          value: 'Code Review',
          label: 'Code Review'
        }, {
          value: 'Other',
          label: 'Other'
        }],
            auth: {
                name: '',
                contact: '',
                description: '',
                type: '',
                authtype: ''
            },
            ruleValidate: {
               type: [
                    { required: true, message: 'Project type required', trigger: 'blur' }
                ],
                name: [
                    { required: true, message: 'Fullname required', trigger: 'blur' }
                ],
                contact: [
                    { required: true, message: 'Email / phone number required', trigger: 'blur' }
                ],
                description: [
                { required: true, message: 'Project description required', trigger: 'blur' }
              ],
            }
        }
    },
    /* beforeCreate: function () {
        if (this.$session.has('langDef')) {
            this.$router.push('/'+this.$session.get('langDef'))
        }
    }, */
    created (){
      this.autoRoute()
      console.log(this.hash)
        /* if (this.$session.has('langDef')) {
            this.curlang = this.$session.get('langDef').toString()
        } else { */
        this.curlang = 'en'
        // }
    },
    watch: {
      langpass: function (val) {
        this.curlang !== val ? this.curlang = val : this.curlang
      },
      '$route': function(){
        this.autoRoute()
      },
      navBtn: function (val){
        this.navBtn = val
      }
    },
    methods: {
      autoRoute () {
            let view = window.location.hash
            if (view === '#hire-us') {
              this.hireDialog = true
            } else if (view === '#estimate'){
              this.estimateDialog = true
            }
        },
       submitForm(formName) {
         console.log(formName)
        this.$refs[formName].validate((valid) => {
          var message = 'I need estimates of building a ' + this.auth.type + ' ~ My name is ' + this.auth.name + ' ~ reach me at ' + this.auth.contact + ' ~ project description ' + this.auth.description
          var payload = {
            text: message
          }
          var urls = 'https://hooks.slack.com/services/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'
          var msg = 'Thanks for visiting, our team will reach out to you within the next 6 - 12 hours'
          if (valid) {
            axios({
  method: 'post',
  url: urls,
  data: JSON.stringify(payload) 
}).then(response => {
  if(formName === 'hire') {
    this.hireDialog = false
  }
  if(formName === 'auth') {
    this.estimateDialog = false
  }
  this.resetForm(formName)
      // JSON responses are automatically parsed.
      this.$message({
        message: msg,
        type: 'success'
      })
    })
    .catch(e => {
      this.errors.push(e)
    })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      handlehr() {
        window.location.hash = 'done'
        this.hireDialog = false
      },
      handleest() {
         window.location.hash = 'done'
        this.estimateDialog = false
      },
      handleSetLanguage (lang) {
        this.$i18n.locale = lang
        // this.$store.dispatch('setLanguage', lang)
        this.$message({
          message: this.$t('language.msg'),
          type: 'success'
        })
      }
    }
}
</script>
<style scoped>
.menu-sm-footer{
  float: left;
  margin-bottom: 20px;
}
.menu-sm-footer-wrap{
  padding: 20px;
}
.ms-side-menu {
  float: right;
  outline: none;
}
.navbar {
  position: absolute;
  width:100% !important;
  left:0 !important;
  height: 50px;
  line-height: 50px;
  border-radius: 0px !important;
  z-index:100 !important;
  background: transparent !important;
  border: none !important;
  padding: 0px 30px;
}
.right-menu {
  float: right;
  height: 100%;
}
.right-menu:focus{
  outline: none;
}
.right-menu-item {
  display: inline-block;
  margin: 0 8px;
}
.left-menu {
  width: calc(100% - 600px);
  padding-top:3px;
  padding-left:30px;
  float: left;
  height: 100%;
}
.left-menu:focus{
  outline: none;
}
.left-menu-item {
  display: inline-block;
  margin: 0 8px;
}
.international{
    float: right;
    margin-top: 5px;
    margin-left: 15px;
}
.el-dropdown-link {
  cursor: pointer;
  color: #fff;
}
.el-icon-arrow-down {
  font-size: 12px;
}
@media (max-width:1179px) {
  .right-menu, .left-menu{
    display:none;
  }
}
.lebs {
  cursor: pointer;
  display: block;
  height: 2rem;
  width: 2rem;
  position: relative;
  float: right;
  font-size: 18px;
  margin: 5px 0px 0px 30px;
  color: #fff;
  cursor: pointer;
}
.lebs span {
  border-radius: 15%;
  height: 3px;
  width: 20px;
  background-color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-10%) translateX(-50%);
  transition: all 0.3s ease-out;
}
.lebs span:before {
  border-radius: 15%;
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background-color: white;
  top: 200%;
  transition: all 0.3s ease-out;
}
.lebs span:after {
  border-radius: 15%;
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background-color: white;
  top: -200%;
  transition: all 0.3s ease-out;
}
.lebs-grey {
  cursor: pointer;
  display: block;
  height: 2rem;
  width: 2rem;
  position: relative;
  float: right;
  font-size: 18px;
  margin: 5px 0px 0px 30px;
  color: #333;
  cursor: pointer;
}
.lebs-grey span {
  border-radius: 15%;
  height: 3px;
  width: 20px;
  background-color: #333;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-10%) translateX(-50%);
  transition: all 0.3s ease-out;
}
.lebs-grey span:before {
  border-radius: 15%;
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background-color: #333;
  top: 200%;
  transition: all 0.3s ease-out;
}
.lebs-grey span:after {
  border-radius: 15%;
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background-color: #333;
  top: -200%;
  transition: all 0.3s ease-out;
}
.lebsactive span {
  height: 0 !important;
  transition: all 0.3s ease-out !important;
}
.lebsactive span:before {
  height: 3px !important;
  transform: rotate(-45deg) !important;
  transition: all 0.3s ease-out !important;
}
.lebsactive span:after {
  height: 3px !important;
  transform: rotate(45deg) !important;
  transition: all 0.3s ease-out !important;
}

.bar {
  color: white;
  background-color: #4a4e54;
  line-height: 4rem;
  border: 1px solid grey;
}

nav {
  float: right;
  width: 290px;
  transform: translateX(-100%) scale(1, 0.1);
  transform-origin: top right;
  transition: all 0.3s ease-out;
  margin-top: -120px;
  background: #FFFFFF;
  box-shadow: 0 10px 34px 0 rgba(32, 32, 32, 0.5);
  border-radius: 3px;
  visibility: hidden;
  margin-right: -15px;
}
nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
nav ul li {
  border-top: 1px solid #efefef;
  opacity: 0;
  transform: translateX(-100%);
  transition: all 0.6s ease-out;
}
nav ul li a {
  transition: all 0.3s ease-out;
  line-height: 1rem;
  text-decoration: none;
  color: #8a8d92;
  display: block;
  padding: 1rem;
}
nav ul li:first-child {
  border-top: none;
}
nav ul li:hover a {
  color: #69b3fc;
  background-color: #ecf5ff;
  transition: all 0.3s ease-out;
}
nav ul li:hover a i {
  color: #69b3fc;
}

.navBtn:checked ~ nav {
  visibility: visible;
  margin-top: 50px;
  transform: translateX(0);
  transition: all 0.3s ease-out;
}

.navBtn:checked ~ nav ul li {
  opacity: 1;
  transform: translateX(0);
  transition: all 0.6s ease-out;
}

.navBtn {
  display: none;
}
@media (min-width:1180px) {
  .ms-side-menu, nav{
    display:none;
  }
}
</style>






